import React, { useEffect } from 'react';
import { navigate } from "gatsby"

import { logout } from '../../services/auth';
import { LANDING } from '../../constants/routes';

interface SignOutRedirectProps {
  goto?: string;
}

const SignOutRedirect: React.FunctionComponent<SignOutRedirectProps> = ({
  goto
}) => {
  useEffect(() => {
    logout();
    navigate(goto || LANDING); //, {replace: true});
  });

  return (
    <>
      <h5>Logging out...</h5>
    </>
  );
}

export { SignOutRedirect };
